/* ===================== Imports ==================== */
import React from 'react'; // import React to create React component
import './Languages.css'; // import the CSS file
import britain from './static/img/britain.png'
import france from './static/img/france.png'
import germany from './static/img/germany.png'
/* ================================================== */

class Languages extends React.Component {
    constructor() {
        super();
        this.state={
            clicked: false,
            textENG: ["THE 26TH YANKEE DIVISION", "VIRTUAL PILGRIMAGE", "ABOUT US", "CONTACT US"],
            textFRA: ["LA 26E DIVISION YANKEE", "PÈLERINAGE VIRTUEL", "À PROPOS DE NOUS", "CONTACTEZ NOUS"],
            textGER: ["DIE 26. YANKEE-ABTEILUNG", "VIRTUELLE PILGERFAHRT", "ÜBER UNS", "KONTAKTIERE UNS"],
            styles: {
                navBar: {
                    backgroundColor: "rgba(0, 0, 0, 0.5)",
                    width: "100%",
                    height: "5vh",
                    position: "static",
                    zIndex: "2",
                },
                langButton: {
                    float: "right",
                    width: "4.5vw",
                    height: "4.5vh",
                    marginRight: "3vw",
                    padding: "0",
                    background: "transparent",
                    border: "none"
                }
            },
            langButtons: (
                <button id="langButton" onClick={this.langOpen}>
                    <i className="fas fa-language fa-3x" style={{padding:"0"}}></i>
                </button>
                )
        };
    }

    getStyle = () => {
        var { styles } = this.state;

        return styles
    }

    langOpen = () => {
        var { styles } = this.state;

        document.getElementById('langs').style.opacity = "1"
        document.getElementById('langs').style.display = "grid"
        
        this.setState({
            langButtons: (
                <button id="langButton" style={styles.langButton} onClick={this.langClose}>
                    <i className="fas fa-language fa-3x" style={{padding:"0"}}></i>
                </button>
            )
        });

    }

    langClose = () => {
        var { styles } = this.state;
        
        document.getElementById('langs').style.opacity = "0"
        document.getElementById('langs').style.display = "none"
        
        this.setState({
            langButtons: (
                <button id="langButton" style={styles.langButton} onClick={this.langOpen}>
                    <i className="fas fa-language fa-3x" style={{padding:"0"}}></i>
                </button>
            )
        });
    }

    languageHandleEng = () => {
        var { textENG } = this.state;

        var yankee = document.getElementById('yankee');
        var pilgrimage = document.getElementById('pilgrimage');
        var about = document.getElementById('about');
        var contact = document.getElementById('contact');

        console.log(yankee.textContent)

        yankee.textContent = textENG[0];
        pilgrimage.textContent = textENG[1];
        about.textContent = textENG[2];
        contact.textContent = textENG[3]

    }

    languageHandleFra = () => {
        var { textFRA } = this.state;

        var yankee = document.getElementById('yankee');
        var pilgrimage = document.getElementById('pilgrimage');
        var about = document.getElementById('about');
        var contact = document.getElementById('contact');

        yankee.textContent = textFRA[0];
        pilgrimage.textContent = textFRA[1];
        about.textContent = textFRA[2];
        contact.textContent = textFRA[3]

    }

    languageHandleGer = () => {
        var { textGER } = this.state;

        var yankee = document.getElementById('yankee');
        var pilgrimage = document.getElementById('pilgrimage');
        var about = document.getElementById('about');
        var contact = document.getElementById('contact');

        yankee.textContent = textGER[0];
        pilgrimage.textContent = textGER[1];
        about.textContent = textGER[2];
        contact.textContent = textGER[3]

    }

    render() {
        var { styles, langButtons } = this.state;

        return (
            <div className="navBar" style={styles.navBar}>
                <div className="languages" style={{display: "grid", float: "right"}}>
                    {langButtons}
                <div id="langs" style={{display: "none", backgroundColor: "rgba(92, 92, 92, 0.5)", opacity: "0"}}>
                    <button className="eng" style={styles.langButton} onClick={this.languageHandleEng}>
                        <img src={britain} alt="britain icon" style={{width: "50%"}}/>
                    </button>
                    <button className="fra" style={styles.langButton} onClick={this.languageHandleFra}>
                        <img src={france} alt="france icon" style={{width: "50%"}}/>
                    </button>
                    <button className="ger" style={styles.langButton} onClick={this.languageHandleGer}>
                        <img src={germany} alt="germany icon" style={{width: "50%"}}/>
                    </button>
                </div>
                </div>
                
            </div>
        );
    }

}

export default Languages;